.skill-icon{
  color: white;
  display: inline-block;
  font-size: 3.5em;
  line-height: 1em;
  margin: 0.25em;
  transition: all 0.1s ease;
  text-align: center;
}

.skill-icon:hover{
  color: rgb(24, 168, 187);
}


.about-image{
  height: 100%;
  width: 100%;
  border-radius: 1em;
}

.about-image img{
  object-fit: fill;
  width:100%;
  height:100%;
}

#about-page{
  background-color: rgb(53, 185, 147);
  color: white;
}

#about-page hr.star-dark {
  border-color: white;
}


#about-page hr.star-dark:after {
  color: white;
  background-color: rgb(53, 185, 147);
  font-size: 3em;
  top:-.4em;
}

#about-page h2{
  text-align: center;
}
#about-page .rich-text{
  text-align: center;
}



.slider {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 10em;
}
.slider > ul {
  width: 100%;
  height: 100%;
  display: flex;
  list-style: none;
  flex-wrap: nowrap;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  scroll-snap-type: x mandatory; 
  scroll-behavior: smooth;
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
  padding: 0;
  margin:0;      
  box-sizing: border-box;
}

.slider > ul::-webkit-scrollbar{
  display: none;  /* Safari and Chrome */
}

.slider > ul > li {
  display: flex;
  position: relative;
  flex-shrink: 0;
  flex-grow: 1;
  width: 25%;
  height: 100%;
  scroll-snap-align: start;
  align-items: center;
  justify-content: center;
}
.slider > ul > li > .icon-svg {
 
  width: 100%;
  height: 100%;
}

.slider > ul > li > .icon-svg .icon-instance {
 
  width: 100%;
  height: 100%;
  padding: 0;
}

.slider > ul > li > .icon-svg .icon-instance svg {
 
  width: 100%;
  height: 100%;
  padding: 0;
}

.slider > ul > li > .icon-svg .icon-instance svg * {
 
  fill: white;
}


