.slider .slider-control:before{
  
  font-family: "Font Awesome 5 Free";
  display: flex;
  align-items: center;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
  color: #000;
  font-size: 3em;
  position: absolute;
  height: 100%;
  width: 5em;
}

.slider .slider-control:hover:before{
  
color: white;
}



.slider .slider-control{
  display: block;
  height: 100%;
  box-sizing: border-box;
  position: relative;
  z-index: 100;
}

.slider .slider-control.left:before{
  content:"\f053";
}

.slider .slider-control.right:before{
  content:"\f054";
  margin-left: -1.0em;
}


